<template>
  <div>
    <v-container>
      <title-ribbon v-bind="{ title: 'Orientación Psicológica Bono Costo Cero' }" /> <!--titulo-->
      <v-row class="orientacion_up">
        <v-col class="orientacion_up_general">
          <div class="oug_main">
            <div class="oug_text">
              <p class="text-justify">
                La
                <strong>Orientación Psicológica Bono Costo Cero</strong>
                consiste en una sesión previa a la Terapia, en la cual se
                realiza una evaluación inicial al paciente y de acuerdo a esta
                se lo deriva al Terapeuta más adecuado para su caso. Está
                dirigida a aquellas personas que no tienen claridad acerca de su
                problema y/o el tipo de especialista que deben consultar.
              </p>
              <p
                class="text-justify"
                style="margin: 0px;"
              >
                Este servicio podrá ser utilizado una única vez por cualquier
                persona sin importar su previsión de salud, y está disponible en
                nuestras sucursales de Santiago y Viña del Mar.
              </p>
            </div>
          </div>
        </v-col>
      </v-row>
      <splitter />
      <v-row class="orientacion_middle">
        <v-col class="orientacion_middle__reserva col-12 col-lg-6">
          <div class="omr_main">
            <div class="omr_title">
              <h2>
                Reserva de Orientación en Línea
              </h2>
              <hr>
              <span>Fácil, rápido y a un click</span>
            </div>
            <div class="omr_btn">
              <v-btn
                :large="
                  $vuetify.breakpoint.sm ||
                    $vuetify.breakpoint.md ||
                    $vuetify.breakpoint.lg ||
                    $vuetify.breakpoint.xl
                "
                href="https://www.centrodeterapia.cl/horas_online"
                target="_blank"
              >
                RESERVA AQUÍ
                <v-icon
                  :large="
                    $vuetify.breakpoint.sm ||
                      $vuetify.breakpoint.md ||
                      $vuetify.breakpoint.lg ||
                      $vuetify.breakpoint.xl
                  "
                >
                  mdi-calendar-month-outline
                </v-icon>
              </v-btn>
            </div>
          </div>
        </v-col>
        <v-col class="orientacion_middle__formulario col-12 col-lg-6">
          <div class="omf_main">
            <div class="omf_title">
              <h2>
                Te Ayudamos a Reservar la Orientación
              </h2>
              <hr>
              <span>Nos contactaremos con usted a la brevedad </span>
            </div>
            <div class="omf_btn">
              <form
                action=""
                @submit.prevent="validate()"
              >
                <validation-observer
                  ref="observer"
                  v-slot="{ invalid }"
                >
                  <div class="omf_btn_form">
                    <validation-provider
                      v-slot="{ errors }"
                      rules="required|max:2"
                      name="Sucursal"
                    >
                      <v-select
                        v-model="sucursal"
                        :error-messages="errors"
                        :items="sucursales"
                        item-text="Nombre"
                        item-value="CodSucursal"
                        :menu-props="{ maxHeight: '400' }"
                        label="Seleccione Sucursal"
                        style="max-width: 250px;"
                        color="#ffffff"
                        persistent-hint
                        filled
                        dense
                        hide-details
                        dark
                        :height="$vuetify.breakpoint.xs ? 41 : 54"
                      />
                    </validation-provider>
                    <v-btn
                      type="submit"
                      color="warning"
                      :disabled="invalid"
                      :height="$vuetify.breakpoint.xs ? 41 : 54"
                    >
                      Ir
                    </v-btn>
                  </div>
                </validation-observer>
              </form>
            </div>
          </div>
        </v-col>
      </v-row>
      <splitter />
      <v-row class="orientacion__bottom">
        <v-col class="orientacion__bottom_main">
          <v-img
            src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto/terapiacl/general/orientacion-psicologica"
            class="obm_image"
          >
            <div class="obm_image_container">
              <div class="obm-image_container__left" />
              <div class="obm-image_container__right">
                <div class="obm_contactanos">
                  <div class="obm_contactanos__title">
                    <h2>Si tienes dudas, contáctanos</h2>
                  </div>
                  <hr>
                  <div class="obm_contactanos__sucursales">
                    <h2
                      v-for="(item, i) in sucursales"
                      :key="i"
                    >
                      <div>
                        <span
                          class="obm_suc_nombre"
                        >{{ item.Nombre }}: &nbsp;</span>
                        <span class="obm_suc_telefono">{{
                          item.Telefono
                        }}</span>
                      </div>
                    </h2>
                  </div>
                </div>
              </div>
            </div>
          </v-img>
        </v-col>
      </v-row>
      <splitter-volver />
    </v-container>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    name: 'OrientacionPsicologica',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      Splitter: () => import('@/components/web/base/Splitter'),
      SplitterVolver: () => import('@/components/web/base/SplitterVolver'),
    },
    data: function () {
      return {
        sucursal: '',
      }
    },
    computed: {
      ...mapState('web', ['sucursales']),
    },
    methods: {
      validate () {
        this.$router.push({ path: `/orientacion/formulario/${this.sucursal}` })
      },
    },
    metaInfo: {
      title: 'Orientación Psicológica',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
